<template>
  <ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-buttons slot="start">
          <ion-back-button default-href="/family-list"></ion-back-button>
        </ion-buttons>
        <ion-title>新增寵物資料</ion-title>
      </ion-toolbar>
    </ion-header>
    <ion-content class="family-list">
      <ion-grid>
        <ion-row style="justify-content: center">
          <ion-col size="12">
            <div ref="imgEditor"></div>
          </ion-col>
        </ion-row>
        <ion-row>
          <ion-col size="12">
            <ion-item class="input">
              <ion-label>　　寵物圖片　　</ion-label>
              <ion-input accept="image/*" @change="imgFileChange" type="file"></ion-input>
            </ion-item>
          </ion-col>
          <ion-col size="12">
            <ion-item class="input">
              <ion-label>　　寵物名稱　　</ion-label>
              <ion-input v-model.trim="name"></ion-input>
            </ion-item>
          </ion-col>
          <ion-col size="12">
            <ion-item class="input select">
              <ion-label>　　曆別　　</ion-label>
              <ion-select interface="popover" v-model="calendar_type">
                <ion-select-option value="1">國曆</ion-select-option>
                <ion-select-option value="2">農曆</ion-select-option>
              </ion-select>
            </ion-item>
          </ion-col>
          <ion-col size="12">
            <ion-item class="input select">
              <ion-label>　　寵物生日　　</ion-label>
              <ion-datetime displayFormat="YYYY-MM-DD" placeholder="請選擇" v-model="birthday"></ion-datetime>
            </ion-item>
          </ion-col>
          <ion-col size="12">
            <ion-button class="button-3" @click="save">儲存</ion-button>
          </ion-col>
        </ion-row>
      </ion-grid>
    </ion-content>
    <ion-footer>
      <menu-bar></menu-bar>
    </ion-footer>
  </ion-page>
</template>

<script>
import {
  saveFamily
} from "../api";

import Croppie from 'croppie';

import {
  IonBackButton,
  IonButton,
  IonButtons,
  IonCol,
  IonContent,
  IonDatetime,
  IonFooter,
  IonGrid,
  IonHeader,
  IonInput,
  IonItem,
  IonLabel,
  IonPage,
  IonRow,
  IonSelect,
  IonSelectOption,
  IonTitle,
  IonToolbar
} from "@ionic/vue";

import MenuBar from "@/views/MenuBar.vue";

export default {
  components: {
    IonBackButton,
    IonButton,
    IonButtons,
    IonCol,
    IonContent,
    IonDatetime,
    IonFooter,
    IonGrid,
    IonHeader,
    IonInput,
    IonItem,
    IonLabel,
    IonPage,
    IonRow,
    IonSelect,
    IonSelectOption,
    IonTitle,
    IonToolbar,
    MenuBar
  },
  data() {
    return {
      name: "",
      calendar_type: "1",
      birthday: "",
      image: '',
      croppieIns: null,
    };
  },
  methods: {
    async save() {
      let imgData = null
      if(this.croppieIns !== null) {
        imgData = await this.croppieIns.result('base64')
      } else {
        imgData = this.image
      }
      saveFamily({
        name: this.name,
        calendar_type: this.calendar_type,
        birthday: this.birthday.split("T")[0],
        image: imgData,
      }, () => {
        this.$router.go(-1);
      });
    },
    imgFileChange(e) {
      const file = e.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.image = reader.result;
      };
    }
  },
  watch: {
    image() {
      if(!this.$refs.imgEditor) return
      if(this.croppieIns !== null) {
        this.croppieIns.destroy()
        this.croppieIns = null
      }
      const c = new Croppie(this.$refs.imgEditor, {
        viewport: { width: 150, height: 150, type: 'circle' },
        boundary: { width: 300, height: 300 },
      })
      c.bind({
        url: this.image
      })
      this.croppieIns = c
    }
  },
  mounted() {
  }
};
</script>

<style scoped>
ion-grid {
  margin: 20px 20px 0 20px;
}
</style>
